// Funcin para iniciar el proceso de verificacin (renew) del token
export const empezarCheking = () => {
  return async (dispatch) => {
    try {
      console.log("Comenzando la verificacin del token");

      // Realiza una solicitud de verificacin con token
      const respuesta = await fetchConToken("auth/renew");
      const body = await respuesta.json();

      if (body.ok) {
        // Almacena el token y la fecha de inicio en el almacenamiento local
        localStorage.setItem("token", body.token);
        localStorage.setItem("token-inicio-dato", new Date().getTime());

        // Despacha la accin de inicio de sesin con los datos del usuario
        dispatch(
          login({
            uid: body.uid,
            nombre: body.nombre,
          })
        );
      } else {
        // Despacha la accin de finalizacin de la verificacin si no es exitosa
        dispatch(checkingFinalizado());
      }
    } catch (error) {
      console.log(error);
      // Maneja errores si es necesario
    }
  };
};

// Accin para indicar que la verificacin ha finalizado
const checkingFinalizado = () => ({
  type: types.authChekingFinalizado,
});

// Accin de inicio de sesin con los datos del usuario
const login = (usuario) => ({
  type: types.authLogin,
  payload: usuario,
});

// Funcin para iniciar el proceso de cierre de sesin
export const empezarLogout = () => {
  return (dispatch) => {
    // Limpia el almacenamiento local (elimina el token)
    localStorage.clear();

    // Despacha la accin de evento de cierre de sesin
    dispatch(eventoLogout());

    // Despacha la accin de cierre de sesin
    dispatch(logout());
  };
};

// Accin de cierre de sesin
const logout = () => ({
  type: types.authlogout,
});
